.App {
  text-align: left;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.description {
  max-width:80%
}

iframe {
  min-width: 850pt;
  width:100%;
  min-height:800px;
}

.tutorialSection {
  width:90%;
  margin-left:5%;
  margin-bottom:25px;
}